.my-4 {
    margin-top: 1rem;
    margin-bottom: 1rem
}

.-mt-16 {
    margin-top: -4rem
}

.mr-4 {
    margin-right: 1rem
}

.flex {
    display: flex
}

.h-\[200px\] {
    height: 200px
}

.h-screen {
    height: 100vh
}

.flex-col {
    flex-direction: column
}

.items-center {
    align-items: center
}

.justify-center {
    justify-content: center
}

.rounded {
    border-radius: .25rem
}

.bg-green-500 {
    --tw-bg-opacity: 1;
    background-color: rgb(34 197 94 / var(--tw-bg-opacity))
}

.bg-red-500 {
    --tw-bg-opacity: 1;
    background-color: rgb(239 68 68 / var(--tw-bg-opacity))
}

