    padding-top: .5rem;
    padding-bottom: .5rem
}

.text-4xl {
    font-size: 2.25rem;
    line-height: 2.5rem
}

.font-bold {
    font-weight: 700
}

.text-white {
    --tw-text-opacity: 1;
    color: rgb(255 255 255 / var(--tw-text-opacity))
}

.hover\:bg-green-700:hover {
    --tw-bg-opacity: 1;
